/**/
.main-slider {
  position: relative;
  overflow: hidden;
  border: 4px solid $color-light;
  border-radius: 3px;
  &__title {
    position: absolute;
    right: 0;
    bottom: 46px;
    border: 4px solid $color-light;
    border-right: 0;
    background-color: $color-white;
    z-index: 10;
    font-size: rem(22);
    font-family: $font-roboto-light;
