/* menu */
[role="navigation"] {
  ul {
    margin: 0;
    padding: 0;
    li {
        list-style: none;
        margin: 0; } } }

ul, ol {
  margin: 0 0 20px 0;
  padding-left: 20px;
  &.list {
    li {
      line-height: 1.87; } } }

ul.list, ol.list {
  padding-left: 0;
  li {
    position: relative;
    padding-left: 23px;
    list-style: none;
    &::before {
      content: '';
      width: 7px;
      height: 7px;
      background-color: $color-blue;
      border-radius: 50%;
      position: absolute;
      left: 8px;
      top: 9px; } } }

ol.list {
  li {
    &::before {
      display: none; }
    .span {
      position: absolute;
      width: 17px;
      height: 17px;
      line-height: 17px;
      background-color: $color-blue;
      border-radius: 50%;
      left: 0;
      top: 4px;
      color: $color-white;
      font-size: rem(10);
      font-family: $font-roboto-light;
      text-align: center; } } }

.catalog__navigation {
  margin-bottom: 20px;
  ul {
    li {
      line-height: 1.2;
      margin-bottom: 5px;
      &.active {
        &>a {
          color: $color-blue; } } }
    a {
      color: $primary-color;
      text-decoration: none;
      &:hover {
        color: $color-blue; } } }
  .ul {
    .li {
      margin-bottom: 10px;
      &--level1 {
        &>a, &>span {
          font-size: rem(15);
          font-family: $font-roboto-bold;
          position: relative;
          &::after {
            content: "\f209";
            position: absolute;
            font-family: $font-ionicons;
            right: 15px;
            top: 0;
            color: $color-white;
            font-size: rem(13);
            margin-top: 6px;
            opacity: 0; } }
        &.is-active {
          &>a, &>span {
            &::after {
              content: "\f218";
              color: $color-blue; } } } } }
    &--level2 {
      padding-top: 8px;
      padding-bottom: 10px;
      &>li {
        &>a, &>span {
          font-size: rem(13);
          display: inline-block;
          padding-left: 16px;
          position: relative;
          &::before {
            content: "\f10a";
            font-family: "Ionicons";
            position: absolute;
            left: 0;
            top: 0;
            color: $color-blue;
            font-size: rem(14); } } } } } }

/* navigation */
.navigation {
  background-color: $color-blue;
  border-radius: 35px;
  overflow: hidden;
  ul {
    li {
      a {
        color: $color-white; } } } }

/* animation */
@keyframes show {
  0% {
    opacity: 0;
    display: none; }
  100% {
    opacity: 1;
    display: block; } }
